.layout__main__right, .layout__main__left {
  background-color: lightgoldenrodyellow;
  @include scut-border(5px solid, n n n y);
  @include scut-padding(8px); }


.layout__side__left, .latoyt__side__right {
  background-color: lightpink;
  @include scut-reset();
  @include scut-size(100% 90px);

  .content {
    background-color: #fff; } }


.layout__wrapper {
  display: flex;
  flex-direction: column;
  @include susy-breakpoint($bk-728) {
    flex-direction: row; }

  .layout__main__left, .layout__main__right {
    flex-basis: 70%; }

  .layout__side__left, .latoyt__side__right {
    flex-basis: 30%; } }

.eq {
  @include eq('small') {
    background-color: red; }

  @include eq('medium') {
    background-color: green; }

  @include eq('large') {
    background-color: lightskyblue; } }
